<template>
  <div class="pos">
    <el-row>
      <el-col :span="7" class="pos-order" id="order-list">
        <el-tabs style="padding:10px">
          <el-tab-pane label="点餐">
            <el-table :data="tableData" border>
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="count" label="数量" width="70"></el-table-column>
              <el-table-column prop="price" label="金额" width="70"></el-table-column>
              <el-table-column label="操作" fixed="right" width="100">
                <template slot-scope="scope">
                  <el-button @click="delS(scope.row)" type="text" size="small">删除</el-button>
                  <el-button type="text" size="small" @click="add(scope.row)">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="total">
              <small>数量:</small>
              {{totalCount}}&nbsp;&nbsp;&nbsp;&nbsp;
              <small>金额:</small>
              {{totalMoney}}元
            </div>
            <div style="text-align:center;margin-top:10px">
              <el-button type="warning" round>挂单</el-button>
              <el-button type="danger" round @click='delAll'>删除</el-button>
              <el-button type="success" round @click='check'>结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">
            <div style="text-align:center;margin-top:10px">
              <el-button type="warning" round>挂单</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="外卖">
            <div style="text-align:center;margin-top:10px">
              <el-button type="success" round>外卖</el-button>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span="17">
        <div class="re-goods">
          <div class="title">热门商品</div>
          <div class="list">
            <ul>
              <li v-for="(goods,index) in reGoods" :key="index" @click="add(goods)">
                <span>{{goods.goodsName}}</span>
                <span style="color:#58b7ff;">￥{{goods.price}}</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="goods-type">
          <el-tabs>
            <el-tab-pane label="汉堡">
              <div>
                <ul class="cook-list">
                  <li v-for="(goods,index) in Goods1" :key="index" @click="add(goods)">
                    <span style="width:40%">
                      <img :src="goods.goodsImg" width="100%">
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="零食">
              <div>
                <ul class="cook-list">
                  <li v-for="(goods,index) in Goods2" :key="index" @click="add(goods)">
                    <span style="width:40%">
                      <img :src="goods.goodsImg" width="100%">
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <div>
                <ul class="cook-list">
                  <li v-for="(goods,index) in Goods3" :key="index" @click="add(goods)">
                    <span style="width:40%">
                      <img :src="goods.goodsImg" width="100%">
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <div>
                <ul class="cook-list">
                  <li v-for="(goods,index) in Goods4" :key="index" @click="add(goods)">
                    <span style="width:40%">
                      <img :src="goods.goodsImg" width="100%">
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableData: [],
      reGoods: [],
      Goods1: [],
      Goods2: [],
      Goods3: [],
      Goods4: [],
      totalMoney: 0,
      totalCount: 0
    };
  },
  created() {
    axios
      .get(
        "https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods"
      )
      .then(res => {
        if (res.status == 200) {
          this.reGoods = res.data;
        }
      })
      .catch(err => {
        console.log("404");
      });
    axios
      .get("https://www.easy-mock.com/mock/5cd6398b7f8f72433eeaa0f2/mock/pos")
      .then(res => {
        if (res.status == 200) {
          this.Goods1 = res.data.data[0];
          this.Goods2 = res.data.data[1];
          this.Goods3 = res.data.data[2];
          this.Goods4 = res.data.data[3];
        }
      })
      .catch(err => {
        console.log("403");
      });
  },
  mounted() {
    var orderHeight = document.body.clientHeight;
    document.getElementById("order-list").style.height = orderHeight + "px";
  },
  methods: {
    add(goods) {
      this.totalMoney = 0;
      this.totalCount = 0;

      let isH = false;
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].goodsId == goods.goodsId) {
          isH = true;
        }
      }
      //no
      if (isH) {
        let arr = this.tableData.filter(a => a.goodsId == goods.goodsId);
        arr[0].count++;
      } else {
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tableData.push(newGoods);
      }
      this.getAllMoney();
    },
    delS(goods) {
      this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
      this.getAllMoney();
    },
    delAll(){
      this.tableData=[];
      this.totalCount = 0;
      this.totalMoney = 0;
    },
    check(){
      if(this.totalCount != 0){
        this.tableData=[];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message({
          message:'结账完成！！',
          type:'success '
        })
      }else{
        this.$message.error('没有账单要结账')
      }
    },
    getAllMoney() {
      this.totalCount = 0;
      this.totalMoney = 0;
      if (this.tableData) {
        //jisuan money
        this.tableData.forEach(ele => {
          this.totalCount += ele.count;
          this.totalMoney = this.totalMoney + ele.price * ele.count;
        });
      }
    }
  }
};
</script>

<style scoped>
.pos-order {
  background-color: #f9fafc;
  border-right: 1px solid #c0ccda;
}
.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
}
.list ul li {
  list-style: none;
  float: left;
  border-bottom: 1px solid #e5e9f2;
  padding: 10px;
  margin: 10px;
  background-color: #fff;
  border-radius: 10px;
  cursor: pointer;
}
.goods-type {
  clear: both;
  padding: 10px;
}
.cook-list li {
  list-style: none;
  width: 23%;
  border-bottom: 1px solid #e5e9f2;
  border-radius: 8px;
  height: auto;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}
.cook-list li span {
  display: block;
  float: left;
}
.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.total {
  background-color: #fff;
  padding: 10px;
  border-bottom: 1px solid #d3dce6;
  text-align: center;
}
</style>


